import React,{useContext} from 'react'
import {StateContext} from '../Context/StateContext'
import Jumbotron from '../Jumbotron/Jumbotron';



const GameBase = () => {

const {dogImages} = useContext(StateContext)





        return (
<React.Fragment>
{dogImages.map((dog)=>(
<img src='./assets/dogpic_3.jpg' alt="dog" key={dog.id}/>


))}

                <Jumbotron
                  
                />
            
</React.Fragment>
        )

    };

export default GameBase;
